import styled from 'styled-components';

import { CardTypes } from 'types/card';
import colors, { statusColorMap } from 'constants/colors';

export const CardWaraper = styled.section`
  display: inline-block;
  position: relative;
  z-index: 2;
`;

export const DeckLineCounter = styled.aside`
  float: left;
  position: sticky;
  left: 0;
  width: 0;
  z-index: 8;
  &:before {
    content: '';
    width: 15px;
    height: 100%;
    position: absolute;
    padding-bottom: 40px;
    z-index: -1;
  }
  div {
    color: ${colors.text.onLight.mute};
    width: 11px;
    height: 28px;
    font-size: 9px;
    line-height: 28px;
    text-align: center;
    padding: 0 1px 0 0;
    margin-top: 5px;
    border: 1px solid #ddd;
    border-left: none;
    background: ${colors.header.lite};
    border-radius: 0 4px 4px 0;
    letter-spacing: -0.1em;
    transition: 0.2s;
    cursor: pointer;
    &:first-child {
      margin-top: 85px;
    }
    &:hover {
      border-color: #aaa;
      color: ${colors.text.onLight.bold};
    }
    &:after {
      content: '+';
      letter-spacing: 0;
      display: inline-block;
      background: ${colors.logo.yellow};
      border: inherit;
      padding: 0 5px 0 9px;
      border-radius: inherit;
      z-index: -1;
      position: absolute;
      margin-top: -1px;
      transform: translateX(-100%);
      transition: 0.1s;
    }
    &[title]:after {
      content: attr(title);
    }
    &:hover:after {
      transform: translateX(0%);
    }
  }
`;

export const CardBlockText = styled.div`
  overflow: hidden;
  text-overflow: ellipsis;
`;

export const CardStatusSVG = styled.svg.attrs({
  xmlns: 'http://www.w3.org/2000/svg',
  viewBox: '0 0 300 300',
})`
  background: ${({ type }: { type: CardTypes }) => statusColorMap[type]};
  border-radius: 50%;
  padding: 2px;
  fill: #fff;
`;

export const CardBlockProgress = styled.mark`
  position: absolute;
  display: block;
  left: 4px;
  right: 4px;
  bottom: 3px;
  height: 2px;
  background: transparent;
  overflow: hidden;
  opacity: 0.4;
  border-radius: 0 0 2px 2px;
  transition: 0.1s;
`;

export const CardBlock = styled.button`
  border: none;
  text-align: left;
  box-sizing: content-box;
  font-size: 12px;
  position: absolute;
  background: #fff;
  border-radius: 4px;
  margin: 0 2px;
  line-height: 28px;
  padding: 0 5px 0 5px;
  border: 1px solid #ccc;
  transition: box-shadow 0.1s, opacity 0.1s;
  cursor: pointer;
  &:hover {
    box-shadow: 0 2px 3px -1px rgba(0, 0, 0, 0.3);
  }
  &:hover ${CardBlockProgress} {
    opacity: 1;
  }
  & ${CardStatusSVG} {
    position: absolute;
    width: 0;
    top: 3px;
    right: 3px;
    transform: translate(50%, -50%);
    z-index: 2;
    transition: 0.1s;
  }
  &:hover ${CardStatusSVG} {
    width: 8px;
    opacity: 1;
  }
  &.resized {
    line-height: 28px;
    border: 1px dashed #e2e2e2;
    padding: 0 4px 0 6px;
    cursor: ew-resize;
  }
  &:before,
  &:after {
    content: '';
    position: absolute;
    top: 0;
    width: 5px;
    bottom: 0;
    opacity: 0;
    cursor: ew-resize;
  }
  &:before {
    left: 0;
  }
  &:after {
    right: 0;
  }
  &:focus {
    outline: none;
  }
  & * {
    pointer-events: none;
  }
`;
